body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  color: white;
}

.App-link {
  color: #61dafb;
}
ul{
  padding: 0;
  list-style-type: none;
  min-height: 300px
}
li{
  text-align: left;
  border-bottom: 1px solid #ddd;
  padding: 10px 0px;
  cursor: pointer;
  min-width: 250px;
}
li:hover:after{
  content: '\2A2F';/*cross*/
  color: red;
  font-size: 18px;
  float: right;
  margin-right: 5px;
  margin-top:10px;
 
}
li.completed {
  text-decoration: line-through;
  color:red;
}
  span.uncomplete:after{
   content: '\25A1';
   font-size: 28px;
   padding: 4px;
 }
 span.completed:after{
  content: '\2713';
  font-size: 28px;
  padding: 4px;
  color: green;
}
.container{
  margin-top: 10px;
  display: flex;
  flex-flow: column;
  border: 0px solid red;
  align-items: center
}
li:last-child{
  border:0px;
}
